import uniNavBar from '../../common/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '../../common/common/uploadImages.vue';
import uniPopup from '../../common/uni-popup/uni-popup.vue';

export default {
	components: {
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			addTextObj: ['所有人可见', '仅自己可见'],
			addTextIndex: 0,
			imageList: [],
			text: '',
			type: 'center',
			show: true,
			content: '中间弹出',
			istip: false
		};
	},
	onLoad() {
		this.togglePopup();
	},
	onBackPress() {
		if (this.text || this.imageList.length) {
			if (!this.istip) {
				this.baoCun();
				return true;
			}
		}
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},

		togglePopup() {
			this.$refs['popup'].open();
		},
		baoCun() {
			uni.showModal({
				content: '是否要保存为草稿？',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
					this.istip = true;
					uni.navigateBack({
						delta: 1
					});
				}
			});
		},
		indexSub() {
			console.log('sss');
		},
		toggleIndexText() {
			uni.showActionSheet({
				itemList: this.addTextObj,
				success: res => {
					this.addTextIndex = res.tapIndex;
				}
			});
		},
		getImageList(val) {
			this.imageList = val;
			console.log(val);
		},
		change(e) {
			console.log(e.show);
		},
		cancel() {
			this.$refs['popup'].close();
		}
	}
};
